.banner {
    color: white;
    object-fit:scale-down;
    height: 360px;
    justify-content: space-between;
    width: 1120px;
    overflow: hidden;
    display:flex;
    padding-left:((100vw-1120px/2)/100vw)*100%;

}


.banner__contents {
  width: 1120px;
  padding-top: 17%;
  padding-left:3%;
  height: 190px;
  justify-content: space-between;
  font-family: msSansSerif;
}

.banner__title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
  font-family: msSansSerif;
}

.banner__description {
  width: 45rem;
  line-height: 1.3;
  padding-top: 1rem;
  font-size: 0.8rem;
  max-width: 360px;
  height: 80px;
  font-family: msSansSerif;
}

.banner__button {
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  padding-left: 2rem;
  padding-right: 2rem;
  margin-right: 1rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  background-color: rgba(51, 51, 51, 0.5);
}

.banner__button:hover {
  color: #000;
  background-color: #e6e6e6;
  transition: all 0.2s;
}

.banner--fadeBottom {
  height: 7.4rem;
  background-image: linear-gradient(180deg, transparent, rgba(37, 37, 37, 0.61), #111);
}
/* banner 이미지 밑 부분이랑 Row 첫 줄 이어지는 부분 자연스럽게 gradient 적용 */
