.KaikasPage {
  position: relative;
  padding-left: 15px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 22px;
  margin-bottom: 44px;

  width: 1120px;
  height: 180px;

  justify-content: space-between;

  background: #d9d9d9;
  box-shadow: 10px 10px 4px rgba(0, 0, 0, 0.4);
}

.welcome {
  position: absolute;
  left: 35%;
  top: 22%;

  font-family: w95fa;
  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: 26px;
  text-align: center;

  color: #000000;
}

.profile_info {
  position: absolute;

  left: 17%;
  top: 28%;
}

.picture_area {
  position: absolute;

  left: 5%;
  top: 23%;
}

.profile_picture {
  width: 90px;
  height: 90px;
  border-radius: 70%;
  overflow: hidden;
}

.profile {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.token_info {
  position: absolute;

  left: 57%;
  top: 38%;

  font-family: w95fa;
}

.amount {
  margin-left: 10px;
  margin-right: 10px;

  font-family: msSansSerif;
}

#walletConnectBtn {
  position: absolute;
  left: 42%;
  top: 55%;

  width: 200px;
  height: 25px;

  margin-top: 10px;

  background: #cccccc;
  box-shadow: inset -1px -1px 1px rgba(0, 0, 0, 0.25), inset 2px 2px 2px rgba(0, 0, 0, 0.6);

  font-family: w95fa;
}

#justBtn {
  position: absolute;
  left: 170%;
  top: 23%;

  width: 100px;
  height: 25px;

  background: #cccccc;
  box-shadow: inset -1px -1px 1px rgba(0, 0, 0, 0.25), inset 2px 2px 2px rgba(0, 0, 0, 0.6);

  font-family: w95fa;
}

#network_info {
  font-family: msSansSerif;
}
